<template>
  <nav class="nav">
    <div class="container">
      <div class="row">
        <a href="" class="logo">
          <img :src="logo" alt="" class="logo-img" />
        </a>

        <div class="nav__collapse" :class="{ nav__opened: mobileActive }">
          <ul class="nav__list">
            <li class="nav__list-item">
              <a href="#about" class="nav__list-link">{{ $t("about_us") }}</a>
            </li>
            <li class="nav__list-item">
              <a href="#team" class="nav__list-link">{{ $t("team") }}</a>
            </li>
            <li class="nav__list-item">
              <a href="#services" class="nav__list-link">{{
                $t("services")
              }}</a>
            </li>
            <li class="nav__list-item">
              <a href="#informations" class="nav__list-link">{{
                $t("informations")
              }}</a>
            </li>
          </ul>
          <!--   links: [
      { link: "#about", name: "about_us" },
      { link: "#team", name: "team" },
      { link: "#services", name: "services" },
      { link: "#informations", name: "informations" },
    ], -->
          <div class="nav-right">
            <div class="navbar-notif lang" @click="isLangOpen = !isLangOpen">
              <div class="language">
                {{ languages[langIndex].name }}
              </div>
              <ul v-if="isLangOpen" class="lang-list">
                <li
                  v-for="(lang, index) in languages"
                  :key="lang.name"
                  @click.stop="changeHandler(index, lang.value)"
                >
                  {{ lang.name }}
                </li>
              </ul>
            </div>
            <a href="tel:+998978084466" class="sign-link btn">{{
              $t("call")
            }}</a>
          </div>
        </div>

        <button
          class="nav__collapse-btn"
          @click="navBtnClick"
          :class="{ nav__opened: mobileActive }"
        >
          <span class="nav-btn-line"></span>
          <span class="nav-btn-line"></span>
          <span class="nav-btn-line"></span>
        </button>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: "Nav",

  data: () => ({
    logo: require("@/assets/img/Logo/main-logo.png"),
    langIndex: 0,
    isLangOpen: false,
    mobileActive: false,
  }),
  methods: {
    navBtnClick() {
      this.mobileActive = !this.mobileActive;
    },
    changeHandler(idx, value) {
      localStorage.setItem("locale", value);
      localStorage.setItem("localeIndex", idx);
      this.$i18n.locale = value;
      this.langIndex = idx;
      this.isLangOpen = false;
    },
  },
  computed: {
    languages: () => [
      { name: "O'Z", value: "uz" },
      {
        name: "RU",
        value: "ru",
      },
      { name: "УЗ", value: "kr" },
    ],
  },
  mounted() {
    document.body.addEventListener("click", (e) => {
      e.target.closest(".lang")
        ? (this.isLangOpen = true)
        : (this.isLangOpen = false);
    });
  },
};
</script>

<style lang="scss" scoped>
.nav-right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 200px;
  width: 100%;

@media (max-width: 1070px) {
  flex-direction: column;
  align-items: end;
  row-gap: 14px;
}
  a {
    width: 100%;
    display: inline-block;
  }
}
.navbar-notif {
  margin-right: 25px;
  position: relative;

  span {
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    color: #fff;
    font-size: calc(12px + 5 * (100vw / 1920));
    position: absolute;
    top: -8px;
    right: -8px;
  }

  i {
    font-size: 1.8rem;
    color: #606e77;
    cursor: pointer;
  }

  &__menu {
    position: absolute;
    top: 45px;
    right: 0;
    background: #fff;
    max-height: 0;
    overflow: hidden;
    transition: 0.3s;
    border-radius: 10px;

    width: 250px;
    z-index: 20;

    ul {
      padding: 15px;

      li {
        font-size: 0.8rem;
        color: #606e77;
        font-weight: 600;
        display: flex;
        align-items: flex-start;
        margin-bottom: 10px;

        .time {
          margin-right: 10px;
          font-size: calc(10px + 6 * (100vw / 1920));
        }

        .message {
          margin-top: -2px;
          font-size: calc(10px + 6 * (100vw / 1920));
        }

        &.all {
          margin-bottom: 0;
          justify-content: center;
          position: relative;

          &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 60%;
            height: 1px;
            background: #606e77;
            display: block;
          }

          a {
            margin-top: 10px;
            display: inline-block;
            color: #606e77;
            font-size: calc(10px + 6 * (100vw / 1920));
          }
        }
      }
    }
    &.active-notif {
      max-height: 400px;
      overflow: hidden;
    }
  }
}
.nav {
  width: 100%;
  background: transparent;
  padding: 15px 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  background: rgba(255, 255, 255, 0.564);
  backdrop-filter: blur(50px);

  .row {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
  }
  .logo {
    max-width: 130px;
    width: 100%;
    display: grid;
    place-items: center;
    img {
      width: 100%;
    }
  }

  &__collapse {
    max-width: 850px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 15px;
  }
  &__list {
    max-width: max-content;
    width: 100%;
    display: flex;
    column-gap: 20px;
    align-items: center;
    &-link {
      font-size: 15px;
      color: var(--primary-color);
      font-weight: 500;
      text-transform: capitalize;
      transition: 0.3s;
      &:hover {
        color: var(--secondary-color);
      }
    }
  }

  .sign-link {
    max-width: 150px;
    width: 100%;
    text-align: center;
    transition: all 0.4s ease;

    &:hover {
      background: #fff;
      color: rgb(6, 0, 114);
    }
  }
  &__collapse-btn {
  display: none;
  }
}
@media (max-width: 1070px) {
  .nav {
    padding: 15px 0;
    &__collapse {
      max-width: 170px;
      position: fixed;
      top: 0;
      right: 0;
      flex-direction: column;
      align-items: flex-end;
      height: 100vh;
      row-gap: 15px;
      padding: 15px;
      padding-top: 70px;
      z-index: 20021;
      background: rgba(255, 255, 255, 0.564);
      overflow-y: auto;

      transition: 0.4s;
      transform: translateX(120%);
      &.nav__opened {
        transform: translateX(0);
      }
    }
    &__list {
      flex-direction: column;
      align-items: flex-end;
      row-gap: 10px;

      &-link {
        text-align: right;
      }
    }
    &__links {
      flex-direction: column;
      align-items: flex-end;
      row-gap: 10px;
    }
    &__collapse-btn {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: center;
      width: 40px;
      height: 40px;
      row-gap: 4px;
      border: none;
      background: transparent;
      border-radius: 50%;
      padding: 5px;
      overflow: hidden;
      z-index: 20222;
      .nav-btn-line {
        width: 100%;
        height: 3px;
        background: #000;
        border-radius: 6px;
        transition: 0.4s;
      }
      .nav-btn-line:last-of-type {
        width: 60%;
      }
      .nav-btn-line:nth-of-type(2) {
        width: 80%;
      }
      &.nav__opened {
        .nav-btn-line:last-of-type {
          width: 100%;
        }
        .nav-btn-line:nth-of-type(2) {
          width: 100%;
        }
      }
    }
  }
}
