.lang {
  cursor: pointer;
  padding: 5px;
  border-radius: 3px;
  font-size: 1rem;

  color: #000;

  &-list {
    position: absolute;
    background: white;
    color: #000000;
    z-index: 50;
    bottom: -88px;
    padding: 10px;
    right: -9px;
    cursor: pointer;
    border-radius: 3px;

    @media (max-width: 1071px){
          bottom: 32px;
    padding: 10px;
    right: -6px;
    }

    li {
      display: flex;
      align-items: center;

      &:not(:last-child) {
        margin-bottom: 5px;
      }

      img {
        width: 30px;
        height: 18px;
        margin-right: 5px;
        border: 0.5px solid #ccc;
      }
    }
  }
}
</style>