<template>
  <swiper
    :slidesPerView="1"
    :spaceBetween="30"
    :loop="true"
    :pagination="{
      clickable: true,
    }"
    :modules="modules"
    class="mySwiper"
  >
    <swiper-slide v-for="slide in doctors" :key="slide.id" class="swiper-slide">
      <img :src="slide.imgUrl" alt="" />
    </swiper-slide>
  </swiper>
</template>
<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";

import "swiper/css/pagination";
import "swiper/css/navigation";

// import required modules
import { Pagination } from "swiper";

export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  data: () => ({
    doctors: [
      {
        imgUrl: require("@/assets/img/doctors/zero.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/first.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/second.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/third.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/fourth.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/fifth.png"),
      },
    ],
  }),
  setup() {
    return {
      modules: [Pagination],
    };
  },
};
</script>

<style lang="scss" scoped>
.swiper {

  height: 100%;
   max-width: 760px;
  width: 100%;
  border-radius: 8px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
 

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
</style>
