    <Carousel :itemsToShow="3.95" :wrapAround="true" :transition="500">
      <Slide v-for="slide in doctors" :key="slide.id">
        <div class="carousel__item">
          <img :src="slide.imgUrl" alt="" />
        </div>
      </Slide>
      <template #addons>
        <Navigation />
        <Pagination />
      </template>
      ...
    </Carousel>
  </div>
</template>

<script>
import { Carousel, Pagination, Slide } from "vue3-carousel";

import "vue3-carousel/dist/carousel.css";

export default {
  name: "Autoplay",
  components: {
    Carousel,
    Slide,
    Pagination,
  },

  data: () => ({
    name: "Autoplay",
    doctors: [
      {
        imgUrl: require("@/assets/img/doctors/zero.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/first.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/second.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/third.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/fourth.png"),
      },
      {
        imgUrl: require("@/assets/img/doctors/fifth.png"),
      },
    ],
  }),
};
</script>

<style lang="scss" scoped>
.slider {
  width: 100%;
}
.carousel__item {
  height: 500px;
  max-width: 450px;
  width: 100%;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 950px) {
    height: 300px;
  }

  @media (max-width: 500px) {
    height: 200px;
  }

  img {
    max-width: 320px;
    width: 100%;
    border-radius: 8px;
  }
}

.carousel__slide {
  padding: 5px;
}

.carousel__viewport {
  perspective: 2000px;
}

.carousel__track {
  transform-style: preserve-3d;
}

.carousel__slide--sliding {
  transition: 0.5s;
}

.carousel__slide {
  opacity: 0.9;
  transform: rotateY(-20deg) scale(0.9);
}

.carousel__slide--active ~ .carousel__slide {
  transform: rotateY(20deg) scale(0.9);
}

.carousel__slide--prev {
