  transform: rotateY(-10deg) scale(0.95);
}

.carousel__slide--next {
  opacity: 1;
  transform: rotateY(10deg) scale(0.95);
}
.carousel__pagination-item {
}
.carousel__slide--active {
  opacity: 1;
  transform: rotateY(0) scale(1.1);
}
.slider-wrapper {
  max-width: 500px;
  width: 100%;

  img {
    width: 100%;
  }
}
</style>